.download-link{
    text-decoration: underline;
    text-underline-offset: 10px;
    transition: $transition-base; 
    span{
        color: var(--#{$prefix}heading-color);
        transition: $transition-base;  
    }
    &:hover{
        span{
            background-color: var(--#{$prefix}primary);
            color: var(--#{$prefix}white);
        }
        .arrow-down-right{
            svg{                
                animation: arrowDownRight .7s alternate-reverse;
            }
        }
        .arrow-up-right{
            svg{                
                animation: arrowUpRight .7s alternate-reverse;
            }
        }
    }
}


.calendar {
    position: relative;
    bottom: 2px;
}

.radio-checked-bg {
    --#{$prefix}headings-color: #d2d2d2;
    --#{$prefix}table-color: var(--#{$prefix}headings-color);
    --#{$prefix}table-bg: linear-gradient(276deg, var(--#{$prefix}primary) 0%, var(--#{$prefix}secondary) 100%);
    --#{$prefix}table-border-color: linear-gradient(276deg, var(--#{$prefix}primary) 0%, var(--#{$prefix}secondary) 100%);     
    background: linear-gradient(276deg, var(--#{$prefix}primary) 0%, var(--#{$prefix}secondary) 100%);
}

.ticket-arrow{    
    --#{$prefix}circle-fill-color: rgba(0, 0, 0, 0.19);
    display: flex;
    align-items:center;
    justify-content: center;
    width: 56px;
    height: 56px;
    background-color: var(--#{$prefix}circle-fill-color);
    border-radius: 50%;    
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    overflow: hidden;
    
    &:hover{
        background-color: var(--#{$prefix}primary);
        color: var(--#{$prefix}heading-color);
        &.arrow-down-right{
            svg{                
                animation: arrowDownRight .7s alternate-reverse;
            }
        }
        &.arrow-up-right{
            svg{                
                animation: arrowUpRight .7s alternate-reverse;
            }
        }
        &.arrow-style-2{            
            color: var(--#{$prefix}white) !important;
        }
    }
    &.arrow-style-2{
        width: 78px;
        height: 78px;
        color: var(--#{$prefix}primary);        
    }
}


.straight-line-wrapper{
    padding-left: 50px;
    transition: all 0.4s ease-in-out;
    font-family: var(--#{$prefix}font-family-style-4);
    color: var(--#{$prefix}heading-color);
    .straight-line{
        position: absolute;
        top: 50%;
        left: 0;
        width: 34px;
        height: 2px;
        z-index: 50;
        background: var(--#{$prefix}primary);
        transform: scaleX(1);
        transform-origin: bottom left;
        transition: all 0.4s ease-in-out;        
    }
    &:hover{
        padding-left: 80px;
        .straight-line{
            transform: scaleX(2);
            transform-origin: bottom left;
        }        
    }
}

.arrow-up-short{
    transform: rotate(45deg);
    position: relative;
    top: -5px;
    transition: $transition-base;
    svg{
        fill: currentColor;
    }
}

.gradient-btn-arrow{
    transform: rotate(0deg);
}

.circle-wrapper{
        --#{$prefix}circle-color: #d9d9d9;
        --#{$prefix}circle-bg: rgba(217, 217, 217, 0.10);
        position: absolute;
        width: 168px;
        height: 168px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        @include media-breakpoint-down(md) {
            top: -52px;
        }

        .circle-image{
            position: absolute;
            width: 170px;
            height: 170px;        
            border-radius: 50%;
            background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='100' ry='100' stroke='%23d2d2d2' stroke-width='2' stroke-dasharray='15%25%2c 13%25' stroke-dashoffset='100' stroke-linecap='butt'/%3e%3c/svg%3e");
            border-radius: 50%;
            background-color: var(--#{$prefix}circle-bg);
            // -webkit-text-stroke-color: var(--#{$prefix}headings-color);
        }

        .circle-bg{
            position: absolute;
            width: 168px;
            height: 168px;
            background-color: var(--#{$prefix}circle-bg);
            border-radius: 50%;
        }
        
        .rotate-text, .rotate-text2, .rotate-text3{
            --#{$prefix}rotate-text-color: var(--#{$prefix}heading-color);
            position: absolute;
            width: 100%;
            height: 100%;
            animation: rotateText 15s linear infinite;        
            p{
                line-height: 3.5;
                color: var(--#{$prefix}rotate-text-color);
            }
            span{
                position: absolute;
                left: 50%;
                font-size: 11px;
                transform-origin: 0 85px; // Always Half the wrapper width
            }
        }
        &::before{
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            top: calc(50% - 10px);
            left: calc(50% - 10px);
            background: var(--#{$prefix}circle-color);
            border-radius: 50%;
            z-index: 999;
            // animation: custom-pulse 2s infinite ease-in-out alternate;
        }    
        
        &.hero-circle{
        top: 0;
        right: 0;
        margin-top: 1rem;      
        backdrop-filter: blur(13px);      
            @include media-breakpoint-down(xxl) {
                margin-top: -2rem;
                top: 0;
                right: 0;
            }
            @include media-breakpoint-down(xl) {
                margin-top: -3rem;
                margin-right: -1rem;
                top: 0;
                right: 0;
            }
            @include media-breakpoint-down(lg) {
                margin-top: -185px;
                right: auto;
                left: 0;
            }
            @include media-breakpoint-down(md) {
                margin-top: -20px;
                right: 20px;
                left: auto;
                width: 100px;
                height: 100px;
                .circle-image{
                    width: 100px;
                    height: 100px;
                }
                .circle-bg{
                    width: 100px;
                    height: 100px;
                }
                .rotate-text{
                    p{
                        line-height: 3.5;
                    }
                    span{                            
                        font-size: 5px;
                        transform-origin: 0 50px; // Always Half the wrapper width
                    }
                }
            }               
        
        }

        &.hero-2-circle{        
            --#{$prefix}circle-wrapper-border-color: rgba(255, 255, 255, .20);
            bottom: 0;
            right: 0;
            margin-right: -302px;
            margin-bottom: 138px;
            width: 238px;
            height: 238px;

            .circle-bg{
                display: none;
            }
            &::before{
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                border-radius: 50%;
                z-index: 0;
                border-style: solid;
                border-width: 44px;
                border-color: var(--#{$prefix}circle-wrapper-border-color);
                background-color: transparent;
            }
            .star-icon{
                position: absolute;
                top: calc(50% - 40px);
                left: calc(50% - 40px);
                // background: red;
                border-radius: 50%;
                width: 80px;
                height: 80px;
                display: flex;
                justify-content: center;
                align-items: center;
            }        
            .rotate-text, .rotate-text2, .rotate-text3{
                --#{$prefix}rotate-text-color: var(--#{$prefix}white);                       
                p{
                    line-height: 2.5;                    
                }
                span{
                    font-size: 16px;
                    transform-origin: 0 119px; // Always Half the wrapper width
                }
            }  

            @include media-breakpoint-down(lg) {
                margin-right: -28px;   
                margin-bottom: 100px; 
                
                width: 130px;
                height: 130px;
                &::before{
                    border-width: 25px !important;
                }                
                .rotate-text{
                    p{
                        line-height: 2.7;
                    }
                    span{                            
                        font-size: 7px;
                        transform-origin: 0 65px; // Always Half the wrapper width
                    }
                }
            }
            @include media-breakpoint-down(md) {
                margin-top: -72px;
                right: auto;
                left: 0;
                width: 100px;
